@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: 'PublicaPlay';
  src: url('../public/fonts/FaceType-PublicaPlay-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

/* Apply the font to all text by default */
@layer base {
  html {
    font-family: 'PublicaPlay', system-ui, sans-serif;
  }
} 